@import "../../assets/scss/common.scss";
.Home {
  display: flex;
  flex-direction: column;
  margin: 0px 67px;
  flex: 1;
  .MarginNone {
    margin: 0px;
  }
  .ContainerBody {
    .ContainerBodyRight{
     padding: 0px 0px 0px 26px;
    }
  }

  .Header {
    margin-top: 20px;
  }
  .ProductList {
    display: flex;
    flex: 1;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .Pagination {
    flex: 1;
    flex-direction: row;
    justify-content: center;
  }
}
