@import "../../assets/scss/common.scss";

.CartDetail {
  .ContainerBody {
    flex-direction: column;
    .NavQuickly {
      margin: 10px 0px;
    }
    .Header {
      margin-bottom: 10px;
    }
    .Steper {
      width: 60%;
      margin: auto;
      padding: 10px 0px 20px 0px;
    }

    .Total {
      display: flex;
      flex-direction: row;
      flex: 1;
      justify-content: flex-end;
      padding: 15px;
      .Label {
        font-weight: 700;
        font-size: 20px;
      }

      .Content {
        font-size: 20px;
        margin: -2px 0px 4px 20px;
      }
    }

    .ButtonSend {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      margin-bottom: 15px;
      user-select: none;
      .ButtonLeft,
      .ButtonRight {
        color: $whiteColor;
        text-transform: uppercase;
        border-radius: 5px 5px 5px 5px;
        background-color: $redColor;
        padding: 12px 20px;
        border: none;
        cursor: pointer;
        margin: 5px;
      }
      .ButtonLeft:hover,
      .ButtonRight:hover {
        background: $orangeColor;
        transition: 0.5s ease-out;
      }

      .ButtonLeft:active,
      .ButtonRight:active {
        background: $redColor;
        transition: 0.001s ease-in;
      }
    }
  }
}
